.breadcrumb{
display: flex;
justify-content: flex-start;
gap: 12px;
margin-left: 16px;
}

.breadcrumb-item {
  margin-top: 25px;
  list-style: none;
  color: #89939A;
  margin-bottom: 40px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 600;
}

.breadcrumb-link {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 600;
  color: #89939A;
  text-decoration: none;
}

.breadcrumb-link:hover {
  text-decoration: underline;
  color:#313237;
}

.breadcrumb-button {
  display: flex;
  align-items: center;
  gap: 9px;
  margin-bottom: 16px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 700;
  font-size: 12px;
  color: #89939A;
  background-color: white;
  border: none;
  cursor: pointer;
  margin-left: 16px;

}

.breadcrumb-button:hover {
  color:#313237;
}

.vector {
padding-right: 12px;
}
